@font-face {
  font-family: Lato;
  src: url(/static/media/Lato-Light.72443183.woff2) format('woff2');
}

body, html {
  padding: 0;
  margin: 0;
  color: #212529;
  background-color: #000;
  font-family: 'Lato', Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 20px;
}

.unstyled {
  list-style: none;
  padding: 0;
  margin: 0;
}

.btn {
  background-color: #007bff;
  padding: 6px 12px;
  font-family: inherit;
  font-size: .75rem;
  font-weight: 400;
  color: #eee;
  border-radius: 3px;
  border: 1px solid transparent; 
  -webkit-transition: all ease-in-out .15s; 
  transition: all ease-in-out .15s;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
}

.btn.formFile {
  background-color: #e9ecef;
  color: #212529;
  border: 1px solid #bbb;
  padding: 6px 12px;
  margin-right: 10px;
}

.btn:hover {
  background-color: #0069d9;
  border-color: #0062cc;
}

.btn.formFile:hover {
  color: #eee;
}

.btn.formSubmit {
  margin-right: 10px;
}

.container {
  width: 80vw;
  margin: auto;
  padding: 10px 0;
  max-width: 980px;
}

.floaty {
  margin: 10px 0;
  background-color: #e9ecef;
  border-radius: 5px;
}

.post {
  padding: 10px;
  -webkit-transition: all ease-in-out .1s;
  transition: all ease-in-out .1s;
  border-bottom: 1px solid #bbb;
}

li:last-child > .post {
  border-bottom: none;
}

.slide-up {
  -webkit-animation: slide-up 0.2s ease;
          animation: slide-up 0.2s ease;
}

@-webkit-keyframes slide-up {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes slide-up {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.loading {
  position: fixed;
  right: 20px;
  bottom: 20px;
  margin: auto;
  width: 32px;
  height: 32px;
  -webkit-transition: all ease-in-out .7s;
  transition: all ease-in-out .7s;
  -webkit-animation: slide-up 0.4s ease;
          animation: slide-up 0.4s ease;
}

.loading > div {
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background-color: #e9ecef;
  -webkit-animation: spin 1s ease-in infinite;
          animation: spin 1s ease-in infinite;
  -webkit-transition: all linear 3s;
  transition: all linear 3s;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: perspective(200px) rotateX(0) rotateY(0);
            transform: perspective(200px) rotateX(0) rotateY(0);
  }
  25% {
    -webkit-transform: perspective(200px) rotateX(180deg) rotateY(0);
            transform: perspective(200px) rotateX(180deg) rotateY(0);
  }
  50% {
    -webkit-transform: perspective(200px) rotateX(180deg) rotateY(180deg);
            transform: perspective(200px) rotateX(180deg) rotateY(180deg);
  }
  75% {
    -webkit-transform: perspective(200px) rotateX(0) rotateY(180deg);
            transform: perspective(200px) rotateX(0) rotateY(180deg);
  }
  100% {
    -webkit-transform: perspective(200px) rotateX(0) rotateY(360deg);
            transform: perspective(200px) rotateX(0) rotateY(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: perspective(200px) rotateX(0) rotateY(0);
            transform: perspective(200px) rotateX(0) rotateY(0);
  }
  25% {
    -webkit-transform: perspective(200px) rotateX(180deg) rotateY(0);
            transform: perspective(200px) rotateX(180deg) rotateY(0);
  }
  50% {
    -webkit-transform: perspective(200px) rotateX(180deg) rotateY(180deg);
            transform: perspective(200px) rotateX(180deg) rotateY(180deg);
  }
  75% {
    -webkit-transform: perspective(200px) rotateX(0) rotateY(180deg);
            transform: perspective(200px) rotateX(0) rotateY(180deg);
  }
  100% {
    -webkit-transform: perspective(200px) rotateX(0) rotateY(360deg);
            transform: perspective(200px) rotateX(0) rotateY(360deg);
  }
}

.loading > div.loadingError {
  background-color: red;
}

.textAlert {
  -webkit-animation: flash 1s ease-out;
          animation: flash 1s ease-out;
}

@-webkit-keyframes flash {
  0% {
    background-color: red;
    color: black;
  }
  100% {
    background-color: #e9ecef;
    color: #bbb;
  }
}

@keyframes flash {
  0% {
    background-color: red;
    color: black;
  }
  100% {
    background-color: #e9ecef;
    color: #bbb;
  }
}

.postTop, .postBottom {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
  font-size: .75rem;
  color: #bbb;
}

.postTop {
  margin-bottom: 10px;
}

.postBottom {
  margin-top: 10px;
}

.postId {
  text-decoration: none;
  color: #bbb;
}

.postLink {
  text-decoration: none;
}

.postId::after {
  content: ' ';
}

.dubs, .op {
  color: green;
}

.postImage {
  max-width: 50%;
}

.postText {
  white-space: pre-wrap;
}

.form {
  padding: 10px;
}

.postForm {
  display: flex;
  flex-direction: column;
}

.formTextarea {
  resize: vertical;
  width: 100%;
  box-sizing: border-box;
  padding: 6px;
  border-radius: 3px;
  border: 1px solid #cdd;
  margin-bottom: 5px;
}

.formButtons {
  display: flex;
  justify-content: space-between;
}

.formMessageBox, .formFilename {
  display: inline-block;
  color: #bbb;
  font-size: .75rem;
  vertical-align: middle;
  padding: 6px 0;
  -webkit-transition: all ease-in-out .1s;
  transition: all ease-in-out .1s;
}
